.Navbar-div {
  position: absolute;
  top: 0;
  left: 0;
  min-height: 60vh;
  width: 100vw;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-start;
  padding: 8px 30px;
  z-index: 10000;
}

.logo {
  height: 6vh;
}
.menu {
  padding-top: 10px;
  height: 3vh;
}
